<template>
    <div id="app">
        <ul>
            <li class='box' 
                v-for="num in nums"
                v-text="num"
                v-getclass="num"
                v-getposition="$index"
                track-by="$index">
            </li>
        </ul>
        <div class="mask" v-show='show===1' @click='show=0' transition='show'>
            <div>{{msg}}</div>
        </div>
        <button @click="reset"></button>
    </div>    
</template>

<script>
export default {
    data () {
        return {
            show:0,
            msg:'',
            pass:false,
            start : {},  //
            nums : []   //16
        }
    },
    ready: function () {
        document.addEventListener('keyup', this.keyDown);
        document.querySelector('#app ul').addEventListener('touchstart', this.touchStart);
        document.querySelector('#app ul').addEventListener('touchend', this.touchEnd);
        //documenttouchmove .box 
        document.addEventListener('touchmove', e=>{
            e.target.classList.contains('box') && e.preventDefault();
        });
        localStorage['save1'] ? this.nums = JSON.parse(localStorage['save1'])
                              : this.reset();
    },
    directives:{
        getclass(value) {
            let classes = this.el.classList;
            [].forEach.call(classes,_=>{
                /^s\w+$/.test(_) && classes.remove(_);
            });
            classes.remove('empty');
            value ? classes.add('s' + value)
                  : classes.add('empty');    
        },
        getposition(index){
            this.el.style.left = index%4*25 + '%';
            this.el.style.top = Math.floor(index/4)*25 + '%';
        }
    },
    methods:{
        /*24 9:1*/
        randomAdd(){
            let arr = this.shuffle(this.blankIndex());
            //100
            setTimeout(_=>{
               this.nums.$set(arr.pop(),Math.random()>0.9 ? 4 : 2); 
            },100);
        },
        /**/
        blankIndex(){
            let arr = [];
            this.nums.forEach((i,j)=>{
                i==='' && arr.push(j);
            });
            return arr;
        },
        /**/
        shuffle(arr){
            let l = arr.length,j;
            while(l--){
                j = parseInt(Math.random()*l);
                [arr[l],arr[j]] = [arr[j],arr[l]]
            }
            return arr;
        },
        /*arrn*/
        /*
            [1,2,   1     [3,1,
            3,4]                     4,2]  
        */
        T(arr,n){
            n=n%4;
            if(n===0)return arr;
            var l = arr.length,d = Math.sqrt(l),tmp = [];
            for(var i=0;i<d;i+=1)
                for(var j=0;j<d;j+=1)
                    tmp[d-i-1+j*d] = arr[i*d+j];
            if(n>1)tmp=this.T(tmp,n-1);
            return tmp;
        },
        touchStart(e){
            this.start['x'] = e.changedTouches[0].pageX;
            this.start['y'] = e.changedTouches[0].pageY;
        },
        touchEnd(e){
            let curPoint = e.changedTouches[0],
                x = curPoint.pageX - this.start.x,
                y = curPoint.pageY - this.start.y,
                xx = Math.abs(x),
                yy = Math.abs(y),
                i = 0;
            // 
            if(xx < 50 && yy < 50)return;    
            if( xx >= yy){ //
                i = x < 0 ? 0 : 2;
            }else{//
                i = y < 0 ? 3 : 1;
            }
            this.handle(i);
        },
        /*
        * 
        * 
        */
        keyDown(e){
            // 0 3 2 1 
            const map = {
                37: 0, //LEFT
                38: 3, //UP
                39: 2, //RIGHT
                40: 1, //DOWN
                87: 3, // W
                68: 2, // D
                83: 1, // S
                65: 0  // A
            };
            if(!(e.keyCode in map))return;
            this.handle(map[e.keyCode]);
        },
        handle(i){
            this.move(i);
            this.save();
            this.isPass();//
        },
        /* i: */
        move(i){
            let indexs = this.T(Object.keys(String(Array(17))),i),//
                tmp = this.T(this.nums,i),//
                hasMove = false, //
                /*
                * 
                * 2 2 4 4  4 8 0 0  8 4 0 0 
                */
                hasCombin = {};
            tmp.forEach((j,k)=>{
                let newIndex = 0,  // 
                    index = indexs[k]-0, //
                    thisMoved = false, //   
                    combinNum = 0; //
                while(k%4 && j!==''){
                    if(tmp[k-1] === ''){ //,
                        tmp[k-1] = j;
                        tmp[k] = '';
                        hasMove = true;
                        thisMoved = true;
                        if(hasCombin[k]){//
                            hasCombin[k-1] = true;
                            hasCombin[k] = false;
                        }
                        newIndex = k-1;
                    }else if(tmp[k-1] === j && !hasCombin[k] && !hasCombin[k-1]){
                        //
                        j *= 2;
                        tmp[k-1] = j;
                        tmp[k] = '';
                        hasMove = true;
                        thisMoved = true;
                        combinNum = j;
                        hasCombin[k-1] = true;  //
                        newIndex = k-1;
                    }else{
                        break;
                    }
                    k--;
                } 
                thisMoved && this.moveNode(index,indexs[newIndex],combinNum);
            });
            setTimeout(_=>{
                this.nums = this.T(tmp,4-i);//this.nums
                hasMove && this.randomAdd();
            },80);
        },
        //indexnextIndex
        moveNode(index,nextIndex,combinNum){
            let allBox = $('.box'),
                curEle = allBox.eq(index),
                nextEle = allBox.eq(nextIndex).clone(),
                clone = curEle.clone(),
                pEle = curEle.parent(),
                box = $('<li class="box empty"></li>');
            box.css({
                left: index%4*25 + '%',
                top: Math.floor(index/4)*25 + '%'
            }).appendTo(pEle);
            clone.appendTo(pEle);
            curEle.css('opacity',0);
            combinNum && nextEle.addClass('combin s'+combinNum).text(combinNum).appendTo(pEle);
            clone.animate({
                left: nextIndex%4*25 + '%',
                top: Math.floor(nextIndex/4)*25 + '%'
            },200,'swing',function(){
                $(this).remove();
                box.remove();
                curEle.css('opacity',1);
                nextEle.remove();
            }); 
        },
        save(){
           localStorage['save1'] = JSON.stringify(this.nums); 
        },
        //
        reset(){
            //[].fill
            //this.nums = Array(16).fill('');
            this.nums = Array(16).join('-').split('-');
            let i =0;
            while(i++<2){ //2
               this.randomAdd(); 
            } 
            this.show = 0;
        },
        isPass(){
            let isOver=true,hasBlank=false,tmp = this.T(this.nums,1);
            this.nums.forEach((i,j)=>{
                if(!i || this.nums[j-4] == i || this.nums[j+4] == i || tmp[j-4] == tmp[j] || tmp[j+4] == tmp[j]){
                    isOver = false;
                }
                if(i==2048 && !this.pass){
                    this.msg = "2048";
                    this.show = 1;           
                    // 2048
                    this.pass = true;
                }
            });
            if(isOver){
                this.msg = "Game Over";
                this.show = 1;
            }
        }
    }
}
</script>

<style>
    @import url(./main.css);
</style>


